<template>
  <div class="upload-photos">
    <div class="upload-body">
    <div v-if="isCanUpload" class="cover-uploader" @click="emitFile">
        <input ref="multipleFile" type="file" accept="image/*" style="opacity: 0; position: fixed; top: -1000px;" multiple="multiple" @change="uploadPhotos">
        <i class="el-icon-plus cover-uploader-icon"></i>
    </div>
    <div class="cover-uploader" v-for="(photo, index) in photoList" :key="photo.pic_id">
        <img :src="photo.pic_url" class="cover-img">
        <div class="cover-uploader-op">
          <i class="el-icon-view" @click="showPrevPhotos(photo.pic_url)"></i> 
          <i class="el-icon-delete" v-if="isCanUpload" @click="delPhoto(index)"></i>          
        </div>
    </div>
    <el-dialog v-model="prevPhotos" size="large">
      <img style="max-width: 100%; max-height: 100%;" :src="prevPhotoUrl" alt="">
    </el-dialog>
    </div>
  </div>
</template>

<script>
import { upload } from "@/api/member";
import Vue from "vue";
import { imgBeforeUpload } from "@/utils";

export default {
  props: {
    photoList: {
      type: Array
    },
    isCanUpload: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      isUploaded: false,
      prevPhotos: false,
      prevPhotoUrl: ""
    };
  },
  methods: {
    emitFile() {
      this.$refs.multipleFile.click();
    },
    uploadPhotos(e) {
      // 文件列表
      let fileList = e.target.files;
      let fileLength = fileList.length;
      // 上传状态
      let uploadState = [];
      for (let i = 0; i < fileLength; i++) {
        uploadState[i] = false;
      }

      let errorList = [];

      for (let i = 0; i < fileLength; i++) {
        // 模拟请求
        /* let num = Math.random() * 700 + 800;
        num = parseInt(num, 10);
        setTimeout(function() {
          _this.uploadState[i] = true;
          _this.photoList[i] = i;
          let state = true;
          for (let j = 0; j < length; j++) {
            if (!_this.uploadState[j]) {
              state = false;
              break;
            }
          }
          if (state) {
            console.log("全部上传完了", _this.uploadState, _this.photoList);
          } else {
            console.log("第 " + i + " 张图片上传完了");
          }
        }, num); */

        let file = fileList[i];

        imgBeforeUpload(file)
          .then(rst => {
            upload(rst.formData).then(response => {
              uploadState[i] = true;
              let photo = {
                pic_url: "http://" + response.data.pic_address,
                pic_id: response.data.pic_id
              };
              this.photoList.push(photo);
              // this.photoList.$set(this.photoList.length, photo);
              // Vue.set(this.photoList, this.photoList.length, photo)
              // this.photoList.splice(this.photoList.length, 1, photo)
              // 强制重新渲染整个列表
              this.$forceUpdate();

              // 判断是否全部上传完
              let state = true;
              for (let j = 0; j < fileLength; j++) {
                if (!uploadState[j]) {
                  state = false;
                  break;
                }
              }
              if (state) {
                this.isUploaded = true;
                let pic_ids_arr = this.photoList.map(photo => photo.pic_id);
                this.$emit("afterUpload", pic_ids_arr.join(","));
                console.log("第 " + i + " 张图片上传完了");
                console.log(
                  "全部上传完了",
                  uploadState,
                  this.photoList,
                  this.isUploaded
                );
                if (errorList.length) {
                  this.$message.error(
                    "第" + errorList.join(", ") + "张图片出错，可能原因是图片大于5MB！"
                  );
                }
              } else {
                console.log("第 " + i + " 张图片上传完了");
              }
            });
          })
          .catch(err => {
            uploadState[i] = true;
            errorList.push(i + 1);
            // 判断是否全部上传完
            let state = true;
            for (let j = 0; j < fileLength; j++) {
              if (!uploadState[j]) {
                state = false;
                break;
              }
            }
            if (state) {
              this.isUploaded = true;
              let pic_ids_arr = this.photoList.map(photo => photo.pic_id);
              this.$emit("afterUpload", pic_ids_arr.join(","));
              console.log("第 " + i + " 张图片上传完了");
              console.log(
                "全部上传完了",
                uploadState,
                this.photoList,
                this.isUploaded
              );
              if (errorList.length) {
                this.$message.error(
                  "第" + errorList.join(", ") + "张图片出错，可能原因是图片大于5MB！"
                );
              }
            } else {
              console.log("第 " + i + " 张图片上传完了");
            }
          });
      }
    },
    showPrevPhotos(pic_url) {
      this.prevPhotos = true;
      this.prevPhotoUrl = pic_url;
    },
    delPhoto(index) {
      this.photoList.splice(index, 1);
      let pic_ids_arr = this.photoList.map(photo => photo.pic_id);
      this.$emit("afterUpload", pic_ids_arr.join(","));
      // 强制重新渲染整个列表
      this.$forceUpdate();
    }
  }
};
</script>

<style lang="scss">
.cover-uploader {
  display: inline-block;
  width: 120px;
  height: 120px;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  margin: 10px;
}
.cover-uploader:hover {
  border-color: #20a0ff;
  .cover-uploader-op {
    opacity: 1;
  }
}
.cover-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.cover-uploader-op {
  opacity: 0;
  transition: opacity 0.3s;
  position: absolute;
  top: 0;
  left: 0;
  font-size: 20px;
  color: #fff;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
  background: rgba(0, 0, 0, 0.3);
}
.cover-img {
  width: 120px;
  height: 120px;
  display: block;
  object-fit: cover;
}

.upload-photos {
  margin-bottom: 20px;
  .upload-body {
    padding: 10px;
    border: 1px solid #bfcbd9;
    font-size: 0;
  }
  .el-dialog__body {
    text-align: center;
  }
}
</style>



