<template>
  <div class="app-container store-show">
      <div class="header">
          <div class="title">门店展示</div>
          <div class="btn-op">
              <el-button type="primary" icon="plus" @click="showDialog">新建相册</el-button>
              <el-button icon="delete" :type="!isOp ? 'danger' : ''" @click="showOP">{{ isOp ? '取消' : '删除' }}</el-button>
          </div>
      </div>

      <el-tabs class="albums" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane v-for="(storeAlbum, index) in albumList" :key="index" :label="storeAlbum ? storeAlbum.store_name : ''" :name="storeAlbum ? storeAlbum.store_name : ''">
            <el-row :gutter="20">
                <el-col :xs="24" :sm="8" :md="6" :lg="4" v-for="(album, index1) in (storeAlbum ? storeAlbum.albums.list : null)" :key="index1">
                    <el-card :body-style="{ padding: '0px' }" :class="{'not-op': !isOp}">
                        <div @click="showAlbum(album.id)">
                            <div class="img-wrap">
                                <img :src="'http://' + album.cover_pic" :alt="album.title">
                            </div>
                            <div class="album-name">{{ album.title }}</div>
                            <div class="btn-delete" v-show="isOp" @click="delAlbum(album.id, album.title)"><i class="el-icon-delete"></i></div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
        </el-tab-pane>
      </el-tabs>

      <el-dialog
        title="新建相册"
        :visible.sync="dialogVisible"
        size="large"
        :before-close="handleClose">
        <el-form ref="form" :model="newAlbumForm" label-width="80px">
            <el-form-item label="相册名称">
                <el-input v-model="newAlbumForm.title"></el-input>
            </el-form-item>
            <el-form-item label="选择门店">
                <el-select v-model="newAlbumForm.store_id" placeholder="请选择门店">
                    <el-option label="英威尔海德店" value="1"></el-option>
                    <el-option label="英威尔龙珠店" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="相册封面">
                <div class="cropper" v-loading="cropLoading">
                  <input ref="file" type="file" accept="image/*" style="opacity: 0; position: fixed; top: -1000px;" @change="cropper">                  
                  <vueCropper
                    v-show="!cover_pic"
                    ref="cropper"
                    :img="option.img"
                    :outputSize="option.outputSize"
                    :outputType="option.outputType"
                    :autoCrop="option.autoCrop"
                    :fixed="option.fixed"
                    :full="option.full"
                    :fixedNumber="option.fixedNumber"
                  ></vueCropper>
                  <img v-show="cover_pic" :src="cover_pic">
                </div>
                <div class="cropper-btn">
                  <el-button v-if="option.img && cover_pic" type="primary" @click="resetCrop">重新裁切</el-button>
                  <el-button v-if="option.img && !cover_pic" type="primary" @click="getCropData" :loading="cropLoading">确认裁切</el-button>
                  <el-button type="primary" @click="emitFile">选择图片</el-button>
                </div>
                <el-input v-model="newAlbumForm.cover_id" v-show="false"></el-input>
            </el-form-item>
        </el-form>
        <div class="upload-photos-title">上传图片</div>
        <upload-photos :photoList="photoList" :isCanUpload="true" @afterUpload="afterUpload"></upload-photos>
        <div style="text-align: right">
            <el-button @click="closeDialog">取消</el-button>
            <el-button type="primary" @click="submitAlbumForm" :disabled="!isCanSubmit">确定</el-button>
        </div>
      </el-dialog>
  </div>
</template>

<script>
import router from "../../router";
import { upload } from "@/api/member";
import { add, lists, dlAlbum } from "@/api/album";
import uploadPhotos from "@/components/uploadPhotos/index";
import { mapGetters } from "vuex";
import Vue from "vue";
import VueCropper from "vue-cropper";
import { uploadBase64 } from "@/api/member";
import { imgBeforeUpload } from "@/utils";

export default {
  components: {
    uploadPhotos,
    VueCropper
  },
  data() {
    return {
      activeName: "英威尔龙珠店",
      isOp: false,
      dialogVisible: false,
      newAlbumForm: {
        title: "",
        store_id: "",
        cover_id: "",
        pic_ids: ""
      },
      cover_pic: "",
      // 相册图片
      photoList: [],
      isUploaded: false,
      // 相册列表
      albumList: [],
      // cropper
      option: {
        img: "",
        outputSize: 1,
        outputType: "png",
        autoCrop: true,
        // 开启宽度和高度比例
        fixed: true,
        fixedNumber: [2, 1],
        full: true
      },
      cropLoading: false
    };
  },
  computed: {
    isCanSubmit() {
      return (
        this.newAlbumForm.title &&
        this.newAlbumForm.store_id &&
        this.newAlbumForm.cover_id &&
        this.newAlbumForm.pic_ids
      );
    },
    isCanClose() {
      return !(
        this.newAlbumForm.title ||
        this.newAlbumForm.store_id ||
        this.newAlbumForm.cover_id ||
        this.newAlbumForm.pic_ids
      );
    },
    ...mapGetters(["storeList"])
  },
  watch: {
    isUploaded(val) {
      console.log(val);
    },
    storeList(val) {
      this.getAlbumList();
      console.log("watch");
      console.log(val);
    }
  },
  created() {
    if (this.storeList) {
      this.getAlbumList();
    }
    console.log(this.storeList);
  },
  methods: {
    // 获取所有相册列表
    getAlbumList() {
      let state = this.storeList.map(() => false);
      for (let i = 0, length = this.storeList.length; i < length; i++) {
        let params = {
          store_id: this.storeList[i].id
        };
        lists(params).then(response => {
          state[i] = true;
          let storeAlbum = {
            store_name: this.storeList[i].name,
            store_id: this.storeList[i].id,
            albums: response.data
          };
          // Vue.set
          Vue.set(this.albumList, i, storeAlbum);
          // this.albumList[i] = storeAlbum;

          let isOk = true;
          for (let item of state) {
            if (!item) {
              isOk = false;
              break;
            }
          }
          if (isOk) {
            console.log(this.storeList[i].name + "的相册获取完成");
            console.log("拉取相册完成");
            console.log(this.albumList);
          } else {
            console.log(this.storeList[i].name + "的相册获取完成");
          }
        });
      }
    },
    handleClick() {
      console.log(this.activeName);
    },
    // 显示对应相册的图片
    showAlbum(albumId) {
      if (this.isOp) {
        return;
      }
      console.log(albumId);
      router.push({ name: "相册详情", params: { id: albumId } });
    },
    showDialog() {
      this.dialogVisible = true;
    },
    closeDialog() {
      if (!this.isCanClose) {
        this.$confirm("您所填写的内容未保存，确认关闭？")
          .then(_ => {
            this.dialogVisible = false;
            this.resetAlbumForm();
          })
          .catch(_ => {});
      } else {
        this.dialogVisible = false;
        this.resetAlbumForm();
      }
    },
    submitAlbumForm() {
      add(this.newAlbumForm).then(response => {
        console.log(response);
        this.getAlbumList();
        this.dialogVisible = false;
        this.resetAlbumForm();
      });
    },
    handleClose(done) {
      if (!this.isCanClose) {
        this.$confirm("您所填写的内容未保存，确认关闭？")
          .then(_ => {
            done();
            this.resetAlbumForm();
          })
          .catch(_ => {});
      } else {
        done();
        this.resetAlbumForm();
      }
    },
    resetAlbumForm() {
      this.newAlbumForm = {
        title: "",
        store_id: "",
        cover_id: "",
        pic_ids: ""
      };
      this.photoList = [];
      this.cover_pic = "";
    },
    handleAvatarSuccess(res, file) {
      this.cover_pic = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    emitFile() {
      this.$refs.file.click();
    },
    // 截图
    cropper(e) {
      let file = e.target.files[0];
      if (!file) {
        return;
      }
      imgBeforeUpload(file)
        .then(rst => {
          this.option.img = rst.base64;
          this.cover_pic = "";
        })
        .catch(err => {
          this.$message.error(err.toString());
        });
    },
    // 获取截图
    getCropData() {
      this.$refs.cropper.getCropData(data => {
        this.cropLoading = true;
        uploadBase64({
          base64: data.substr(22)
        }).then(response => {
          this.cropLoading = false;
          console.log("http://" + response.data.pic_address);
          this.cover_pic = "http://" + response.data.pic_address;
          this.newAlbumForm.cover_id = response.data.pic_id;
        });
      });
    },
    // 重新裁切
    resetCrop() {
      this.cover_pic = "";
    },
    // 上传之后触发
    afterUpload(pic_ids) {
      this.newAlbumForm.pic_ids = pic_ids;
      console.log(this.newAlbumForm);
    },
    showOP() {
      this.isOp = !this.isOp;
    },
    delAlbum(id, title) {
      this.$confirm("确定删除相册《" + title + "》？")
        .then(_ => {
          dlAlbum({ id }).then(response => {
            this.getAlbumList();
          });
        })
        .catch(_ => {});
    }
  }
};
</script>

<style lang="scss">
.store-show {
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
    padding: 20px;
    border: 1px solid #e3e8f0;
    margin-bottom: 10px;
    .title {
      font-size: 20px;
    }
  }

  .albums {
    .el-tabs__content {
      background: #fff;
      border: 1px solid #e3e8f0;
      padding: 15px;
    }
    .el-card {
      max-width: 200px;
      margin: 0 auto 20px;
      position: relative;
      border: none;
      transition: 0.2s all;
      .img-wrap {
        position: relative;
        width: 100%;
        padding-bottom: 100%;
        background: #7c7c7c;
        overflow: hidden;
        img {
          position: absolute;
          height: 100%;
          width: 100%;
          object-fit: cover;
          transition: 0.2s all;
        }
      }
      .album-name {
        padding: 0 10px;
        background: #fff;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        font-size: 14px;
        height: 36px;
        line-height: 36px;
      }
      .btn-delete {
        position: absolute;
        top: 5px;
        left: 5px;
        color: #ff4949;
        cursor: pointer;
      }
      &.not-op {
        cursor: pointer;
        &:hover {
          box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3),
            0 0 6px 0 rgba(0, 0, 0, 0.1);
          transform: translateY(-8px);
          img {
            transform: scale(1.2);
          }
        }
      }
    }
  }

  .cover-uploader {
    width: 120px;
    height: 120px;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .cover-uploader:hover {
    border-color: #20a0ff;
  }

  .cover-uploader.cover-crop {
    width: 50%;
    margin: 0;
    height: 0;
    padding-bottom: 25%;
  }

  .cover-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
  }

  .cover-uploader.cover-crop .cover-uploader-icon {
    position: absolute;
    width: auto;
    height: auto;
    left: 50%;
    top: 50%;
    line-height: 1;
    transform: translate(-50%, -50%);
  }

  .cover-img {
    width: 120px;
    height: 120px;
    display: block;
  }

  .cropper {
    position: relative;
    width: 100%;
    padding-bottom: 50%;
    margin-bottom: 15px;
  }

  .cropper .vue-cropper {
    position: absolute;
  }

  .cropper > img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
  }

  .el-dialog--large {
    max-width: 650px;
    .el-form {
      margin-bottom: 20px;
      border-bottom: 1px solid #bfcbd9;
    }
    .upload-photos-title {
      width: 80px;
      text-align: right;
      font-size: 14px;
      color: #48576a;
      line-height: 1;
      padding: 11px 12px 11px 0;
      box-sizing: border-box;
    }
    .upload-photos {
      background: #f2f4f8;
    }
  }
}
</style>




