import fetch from '@/utils/fetch1';
const qs = require('qs');

export function add(params) {
    return fetch({
        url: '/api/album/add',
        method: 'post',
        data: qs.stringify(params),
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
}

export function lists(params) {
    return fetch({
        url: '/api/album/lists',
        method: 'post',
        data: qs.stringify(params),
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
}

